import React, { useState } from 'react';
import { Link, } from 'react-router-dom';
import { FaTwitter } from "react-icons/fa"
import Logo from './Images1/abc.ico'



const MainHeader = () => {
const [state,setstate]= useState(false);
    return (
        <>
            <nav>

                <ul className="menu">
                <div className="hamburg">
                     <Link to="#"  onClick={() => setstate(!state)}>   <FaTwitter /> </Link>
                    </div>
                    <li className="logo">
                        <Link to="#"><img src={Logo} alt="yourpic" /></Link>
                    </li>
                    <div className="Logoname">
                        <h1>Unique fitness</h1>
                    </div>
                    <div className={state ?"menumob":"menu1"}>
                        <li><Link to="/contactus">  Contact us</Link></li>
                        <li><Link to="/Aboutus">  About us</Link></li>
                        <li><Link to="/galery">Gallery</Link></li>
                    </div>
                   
                </ul>

            </nav>
        </>
    );
};

export default MainHeader;