import React from 'react';
import { Link, } from 'react-router-dom';
import Logo from './Images1/abc.ico';
import { FaFacebook,FaInstagram ,FaTwitterSquare} from "react-icons/fa"
const MFooter = () => {
    return (
       
       <footer>
       <div className="main-footer">
         <ul>
             <div>
             <li className="logo">
                        <Link to="#"><img src={Logo} alt="yourpic" /></Link>
                    </li>
                   <div className="social-media">
                       <div><h3>Social Media </h3></div>
                       <div className="social-media-link">
                        <li><Link to="/contactus"> <FaFacebook/></Link></li>
                        <li><Link to="/Aboutus">  <FaInstagram/></Link></li>
                        <li><Link to="/galery"><FaTwitterSquare/></Link></li>
                        </div>
                   </div>
             </div>
         
                   
                    <div className="fotter2" >
                        <li><Link to="/contactus">  Contact us</Link></li>
                        <li><Link to="/Aboutus">  About us</Link></li>
                        <li><Link to="/galery">Gallery</Link></li>
                    </div>
         </ul>

       </div>
        </footer>
    );
};

export default MFooter;



